* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(118, 118, 189);
  color: aliceblue;
}

.text-center {
  text-align: center;
}
.container {
  width: 60%;
  margin: 10% auto;
}
.ListItems {
  background-color: thistle;
  color: black;
}
.ListItems p {
  margin: 0;
  text-align: center;
  padding: 10px 0px;
}
.ListItems span {
  display: inline-block;
  text-align: center;
  padding: 10px 0;
  font-size: 20px;
  border: 0.3px solid rgb(204, 156, 156);
}

.ListItems span.title {
  background-color: teal;
  color: aliceblue;
}

.ListItems span.name,
form input[type="text"] {
  width: 60%;
}

.ListItems span.age,
form input[type="number"] {
  width: 30%;
}

.ListItems span.action,
form input[type="submit"] {
  width: 10%;
}
.ListItems span.icon {
  color: aqua;
  cursor: pointer;
}
form input {
  padding: 10px;
  background-color: rgb(192, 192, 141);
  border: transparent;
  border: 0.3px solid rgb(230, 207, 164);
}
